import {IHeroPack} from "../types/IHeroPack";
import React from "react";
import {Box, Button} from "@mui/material";
import HourglassBottomIcon from '@mui/icons-material/HourglassBottom';
import {HeroData} from "../types";
import {SelectedHeroPack} from "./SelectedHeroPack";

interface IProps {
    heroPack: IHeroPack,
    changeHeaderMessageFunction: any,
    mainUrl: string,
    params: any
}

interface IState {
    minWinRate: number,
    minFightsCount: number,
    heroFilter: number[],
    heroPacks: IHeroPack[],
    totalPacksFound: number,
    requestsInProgress: number,
    heroProbs: Map<number, number>
    filterMinWinRate: number,
    filterMaxWinRate: number,
    filterMinFights: number,
    filterMaxFights: number,
    totalFound: number
}


export class CounterPackResult extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        let minFightsCount = 0;
        if (this.props.params.get("minFightsCount")) {
            const x = ~~this.props.params.get("minFightsCount")
            if (x >= 0) {
                minFightsCount = x
            }
        }
        const heroFilter = []
        if (this.props.params.get("heroFilter")) {
            const x = this.props.params.get("heroFilter")?.split(",") || []
            for (let i = 0; i < 5 && i < x.length; i++) {
                if (HeroData.getHeroById(~~x[i])) {
                    heroFilter.push(~~x[i])
                }
            }
        }
        let minWinRate = 0
        if (this.props.params.get("minWinRate")) {
            const x = ~~this.props.params.get("minWinRate")
            if (x >= 0 && x <= 100) {
                minWinRate = x
            }
        }
        this.state = {
            minWinRate: minWinRate,
            minFightsCount: minFightsCount,
            heroFilter: heroFilter,
            heroPacks: [],
            requestsInProgress: 0,
            totalPacksFound: 0,
            heroProbs: new Map<number, number>(),
            filterMinWinRate: 0,
            filterMaxWinRate: 0,
            filterMinFights: 0,
            filterMaxFights: 0,
            totalFound: 0
        }

        this.props.changeHeaderMessageFunction("Загружаем контр-паки из статистики...")
        this.onHeroSelect = this.onHeroSelect.bind(this)
    }

    requestsInProgress: number = 0

    static constructHeroFilter(pHeroFilter: number[]) {
        let result = "%"
        for (let i = 0; i <= 5 && i < pHeroFilter.length; i++) {
            result += ':' + pHeroFilter[i] + ':%'
        }
        return escape(result)
    }

    componentDidMount() {
        this.updateHeroPackInfo()
    }

    updateHeroPackInfo() {
        const heroPack = this.props.heroPack
        if (heroPack.hIds.length === 5) {
            //https://g92e0914be8be53-db40vlv.adb.eu-frankfurt-1.oraclecloudapps.com/ords/hwstat/hws/lostPacks/:h1/:h2/:h3/:h4/:h5/:wr/:cnt/:hf
            let url = "https://g92e0914be8be53-db40vlv.adb.eu-frankfurt-1.oraclecloudapps.com/ords/hwstat/hws/lostPacks/"
            const heroParams = [-1, -1, -1, -1, -1]
            for (let i = 0; i < 5 && i < heroPack.hIds.length; i++) {
                heroParams[i] = heroPack.hIds[i];
            }
            this.requestsInProgress++

            for (let i = 0; i < 5 && i < heroPack.hIds.length; i++) {
                heroParams[i] = heroPack.hIds[i];
            }
            url += heroParams.join("/")
            url += "/" + this.state.minWinRate + "/" + this.state.minFightsCount + "/"
            url += CounterPackResult.constructHeroFilter(this.state.heroFilter)
            console.log('ZZZ', url)
            fetch(url, {
                method: "GET"
            }).then(res => res.json())
                .then(
                    (result) => {
                        if (result['items']) {
                            const heroLostPacks = []
                            let cnt = 0
                            const heroProbs = new Map<number, number>()
                            let filterMinWinRate = 0
                            let filterMaxWinRate = 0
                            let filterMinFights = 0
                            let filterMaxFights = 0
                            let totalFound = 0
                            for (const x in result['items']) {
                                const z = result['items'][x]
                                let lostPack = []
                                lostPack = [~~z.d1, ~~z.d2, ~~z.d3, ~~z.d4, ~~z.d5]
                                filterMinWinRate = z.minwr
                                filterMaxWinRate = z.maxwr
                                filterMinFights = z.minc
                                filterMaxFights = z.maxc
                                totalFound = z.tc
                                for (const heroId of lostPack) {
                                    let m = heroProbs.get(~~heroId)
                                    if (m) {
                                        m++
                                    } else {
                                        m = 1
                                    }
                                    heroProbs.set(~~heroId, m)
                                }
                                cnt++
                                heroLostPacks.push({
                                    hIds: lostPack,
                                    extendedInfo: {
                                        statusInfo: "",
                                        wra: z.wra,
                                        wrd: z.wrd,
                                        ca: z.ca,
                                        cd: z.cd,
                                        wa: z.wa,
                                        wd: z.wd,
                                        cc: z.cc,
                                        cwr: z.cwr
                                    }
                                })
                            }
                            for (const heroPackId of Array.from(heroProbs.keys())) {
                                const mmd = heroProbs.get(~~heroPackId)
                                if (mmd && cnt > 0) heroProbs.set(~~heroPackId, 100 * mmd / cnt)
                            }
                            this.setState({
                                heroProbs: heroProbs,
                                heroPacks: heroLostPacks,
                                filterMinWinRate: filterMinWinRate,
                                filterMaxWinRate: filterMaxWinRate,
                                filterMinFights: filterMinFights,
                                filterMaxFights: filterMaxFights,
                                totalFound: totalFound
                            })
                        }
                    }).finally(() => {
                this.requestsInProgress--
                this.setState({requestsInProgress: this.requestsInProgress})
            })
        }
    }

    onHeroSelect(e: any) {

    }

    renderHeroPacks() {
        const items = []
        const emptyMap = new Map<number, number>()
        for (const i in this.state.heroPacks) {
            items.push(<SelectedHeroPack additionalClasses={[]} heroPack={this.state.heroPacks[i]} heroProbs={emptyMap}
                                         onHeroSelect={this.onHeroSelect} selectedHeroes={this.state.heroFilter}/>)
        }
        return items
    }

    render() {
        if (this.state.requestsInProgress > 0) {
            return (
                <Box className={"counterPackResult"}>
                    Loading...<HourglassBottomIcon/>
                </Box>
            )
        } else {
            if (this.state.heroPacks.length === 0) {
                return (
                    <Box className={"counterPackResult"}>
                        <Box className={"counterPackNotFound"}>
                            <Box>Не нашли контр-паки у себя в статистике боев</Box>
                            <span>Попробовать добавить в очередь на поиск на сервер обработки боёв?</span>
                            <Button>Найти контр-пак</Button>
                        </Box>
                    </Box>
                )
            } else {
                return (
                    <Box className={"counterPackResult"}>
                        <Box className={"counterPackResultStatusBar"}>Нашли {this.state.totalFound} контр-паков</Box>
                        <Box className={"counterPackResultPacks"}>
                            {this.renderHeroPacks()}
                        </Box>
                    </Box>
                )
            }
        }

    }

}