import React, {useEffect, useState} from "react";
import {Box} from "@mui/material";
import {SelectedHeroPack} from "./SelectedHeroPack";
import {HeroData} from "../types";
import {HeroModernGrid} from "./HeroModernGrid";
import {IHeroPack} from "../types/IHeroPack";
import {HeaderBlock} from "./HeaderBlock";
import {CounterPackResult} from "./CounterPackResult";
import {useLocation, useMatch, useNavigate, useParams} from "react-router-dom";

//https://g92e0914be8be53-db40vlv.adb.eu-frankfurt-1.oraclecloudapps.com/ords/hwstat/hws/lostPacks/:h1/:h2/:h3/:h4/:h5/:wr/:cnt/:hf

export function CounterPackScreen(props: any) {
    const params = useParams();
    const match = useMatch("");
    const navigate = useNavigate();
    const location = useLocation();
    const [headerMessage, setHeaderMessage] = useState("Выберите пачку из 5 героев для поиска");
    const templateHeroPack = HeroData.getEmptyHeroPack()
    const queryParams: any = new URLSearchParams(location?.search || "")
    const [isInitialized,setIsInitialized] = useState(false)
    if (queryParams.get("hids")) {
        const x = queryParams.get("hids")?.split(",")
        console.log(templateHeroPack.hIds)
        if (x && x.length <= 5) {
            for (const z of x) {
                templateHeroPack.hIds.push(~~z)
            }
        }
    }

    const [heroPack, setHeroPack] = useState(templateHeroPack);
    const [heroAvailable, setHeroAvailable] = useState([] as number[])
    const [heroProbs, setHeroProbs] = useState(new Map<number, number>())
    const [requestsInProgress, setRequestsInProgress] = useState(0)

    const updateAvailableHeroes = function (pHeroPack: IHeroPack) {
        const heroParams = [-1, -1, -1, -1, -1]
        for (let i = 0; i < 5 && i < pHeroPack.hIds.length; i++) {
            heroParams[i] = pHeroPack.hIds[i];
        }
        setRequestsInProgress(requestsInProgress + 2)
        let url = "https://g92e0914be8be53-db40vlv.adb.eu-frankfurt-1.oraclecloudapps.com/ords/hwstat/hws/findLostPackHeroesStat/"
        fetch(url + heroParams.join("/"), {
            method: "GET"
        })
            .then(res => res.json())
            .then(
                (result) => {
                    let heroArray: number[] = []
                    if (result['items']) {
                        for (const j in result['items']) {
                            const x = result['items'][j]['d']
                            if (x) {
                                if (!heroArray.includes(~~x)) {
                                    heroArray.push(~~x)
                                }
                            }
                        }
                    }
                    setHeroAvailable(heroArray)
                },
                (error) => {
                    console.error("error req", error)
                    setHeroAvailable([])
                }
            ).finally(() => {
            setRequestsInProgress(requestsInProgress - 1)
        })

        url = "https://g92e0914be8be53-db40vlv.adb.eu-frankfurt-1.oraclecloudapps.com/ords/hwstat/hws/packStat/"
        fetch(url + heroParams.join("/"), {
            method: "GET"
        })
            .then(res => res.json())
            .then(
                (result) => {
                    let heroArray: number[] = []
                    if (result['items']) {
                        for (const j in result['items']) {
                            const x = result['items'][j]
                            if (x) {
                                const newHeroPack = HeroData.getEmptyHeroPack()
                                newHeroPack.hIds = pHeroPack.hIds
                                newHeroPack.extendedInfo = {
                                    statusInfo: "",
                                    ca: x.ca,
                                    cd: x.cd,
                                    wra: x.wra,
                                    wrd: x.wrd,
                                    wa: x.wa,
                                    wd: x.wd,
                                }
                                setHeroPack(newHeroPack)
                            }
                        }
                    }
                    setHeroAvailable(heroArray)
                },
                (error) => {
                    console.error("error req", error)
                    setHeroAvailable([])
                }
            ).finally(() => {
            setRequestsInProgress(requestsInProgress - 1)
        })


    }
    const updateHeroPackInfo = function (pHeroPack: IHeroPack) {
        navigate("/counterPack?hids=" + pHeroPack.hIds.join(","))
        setHeroPack(pHeroPack)
        updateAvailableHeroes(pHeroPack)
    }
    const changeHeaderMessage = function (newMessage: string) {
        setHeaderMessage(newMessage)
    }
    const onHeroSelect = function (e: any) {
        if (e?.target?.dataset?.heroid > 0) {
            const heroId = ~~e.target.dataset.heroid
            if (HeroData.allHeroesIds.includes(heroId)) {
                const index = heroPack.hIds.indexOf(heroId)
                if (index < 0) {
                    if (heroPack.hIds.length < 5) {
                        heroPack.hIds.push(heroId)
                        heroPack.extendedInfo = {statusInfo: ""}
                        updateHeroPackInfo(heroPack)
                    }
                } else {
                    heroPack.extendedInfo = {statusInfo: ""}
                    heroPack.hIds.splice(index, 1);
                    updateHeroPackInfo(heroPack)
                }
            }
        }
    }
    useEffect(() => {
        if (!isInitialized && heroPack.hIds.length===5) {
            setIsInitialized(true)
            updateHeroPackInfo(heroPack)
        }
    });

    return (
        <Box className={"counterPackScreen"}>
            <HeaderBlock openMenuFunction={props.openMenuFunction}
                         message={headerMessage}/>
            <SelectedHeroPack heroPack={heroPack} heroProbs={HeroData.zeroHeroProbMap}
                              onHeroSelect={onHeroSelect} selectedHeroes={[]} additionalClasses={[]}/>
            {heroPack.hIds.length < 5 && (
                <HeroModernGrid heroProb={heroProbs} onClick={onHeroSelect}
                                selectedHeroes={heroPack.hIds} highlightedHeroes={heroAvailable}/>
            )}
            {heroPack.hIds.length === 5 && (
                <CounterPackResult changeHeaderMessageFunction={changeHeaderMessage} heroPack={heroPack}
                                   params={queryParams}
                                   mainUrl={"/counterPack" + (heroPack.hIds.length > 0 ? "?hIds=" + heroPack.hIds.join(",") : "")}/>
            )}
        </Box>
    )

}

