import React from "react";
import {Box} from "@mui/material";
import {IHeroPack} from "../types/IHeroPack";
import {HeroModernIcon} from "./HeroModernIcon";
import {HeroData} from "../types";

interface IProps {
    heroPack:IHeroPack,
    heroProbs: Map<number,number>,
    onHeroSelect:any,
    selectedHeroes: number[],
    additionalClasses: string[]
}

interface IState {

}

export class SelectedHeroPack extends React.Component<IProps, IState> {
    /*constructor(props:any) {
        super(props);
    }*/
    renderHeroIcons() {
        this.props.heroPack.hIds.sort((a,b) => {
            return (HeroData.getHeroById(a)?.battleOrder || 0)-(HeroData.getHeroById(b)?.battleOrder||0)
        })
        const items:any=[]
        for(let i=0;i<5;i++) {
            const hId=this.props.heroPack.hIds[i]
            items.push(<HeroModernIcon key={i} additionalClasses={[]} heroId={hId} heroProb={this.props.heroProbs.get(hId) || 0} onClick={this.props.onHeroSelect}/>)
        }
        return items
    }
    render() {
        const additionalClasses="selectedHeroPack" + this.props.additionalClasses.join(" ")
        return (
            <Box className={"selectedHeroPack"}>
                <Box className={"selectedHeroPackIcons"}>
                {this.renderHeroIcons()}
                </Box>
                <Box className={"selectedHeroPackExtended"}>
                    <span><img src={"./trophy_a_w.png"}/><span>{(this.props.heroPack?.extendedInfo?.wra=== undefined?"?":this.props.heroPack?.extendedInfo?.wra)+"%"}</span></span>
                    <span><img src={"./attack_w.png"}/><span>{this.props.heroPack?.extendedInfo?.ca=== undefined?"?":this.props.heroPack?.extendedInfo?.ca}</span></span>
                    <span><img src={"./trophy_d_w.png"}/><span>{(this.props.heroPack?.extendedInfo?.wrd=== undefined?"?":this.props.heroPack?.extendedInfo?.wrd)+"%"}</span></span>
                    <span><img src={"./shield_w.png"}/><span>{this.props.heroPack?.extendedInfo?.cd === undefined?"?":this.props.heroPack?.extendedInfo?.cd}</span></span>
                </Box>
            </Box>
        )
    }
}